import React, { useState } from "react";

const InputNodes = (props) => {
  const [node, setNode] = useState({
    x: 0,
    y: 0,
  });

  const handleChange = (e) => {
    setNode({ ...node, [e.target.name]: e.target.value });
  };

  const handleSubmit = (e) => {
    props.addCoord(node);
    e.preventDefault();
  };

  return (
    <form onSubmit={handleSubmit} name="myform">
      <label>
        X Coordinate:
        <input type="number" name="x" value={node.x} onChange={handleChange} />
      </label>
      <div>
        <label>
          Y Coordinate:
          <input
            type="number"
            name="y"
            value={node.y}
            onChange={handleChange}
          />
        </label>
      </div>
      <div style={{ textAlign: "center", marginTop: "4px" }}>
        <button className="btn btn-primary add-coordinate-button" type="submit">
          Add Coordinate
        </button>
      </div>
    </form>
  );
};
export default InputNodes;
