import "./App.css";
import React, { useState } from "react";
import Compo from "./components/compo";
import Circle from "./components/circle";
import InputNodes from "./components/inputnodes.jsx";
import CoordsList from "./components/coordslist.jsx";
import Solve from "./components/solve.jsx";
import ElementsList from "./components/elementslist.jsx";

export default function App() {
  const [state, setNumber] = useState({ number: 10 });
  const [coords, setCoords] = useState({
    x: [
      [50, 350],
      [300, 350],
      [175, 50],
      [425, 50],
      [550, 350],
    ],
    fixity: [1, 1, 0, 0, 1],
    extf: [
      { x: 0, y: 0 },
      { x: 0, y: 0 },
      { x: 100, y: 0 },
      { x: 0, y: 0 },
      { x: 0, y: 0 },
    ],
  });
  const [connectivity, setConnectivity] = useState({
    nodes: [
      [0, 1],
      [0, 2],
      [1, 2],
      [2, 3],
      [1, 4],
      [1, 3],
      [3, 4],
    ],
    secprops: [
      { E: 1, A: 1 },
      { E: 1, A: 1 },
      { E: 1, A: 1 },
      { E: 1, A: 1 },
      { E: 1, A: 1 },
      { E: 1, A: 1 },
      { E: 1, A: 1 },
    ],
  });

  const [solved, toggleSolved] = useState(0);
  const [solution, setSolution] = useState({ Qu: [], Du: [] });
  const [renders, setRenders] = useState([]);

  const handleIncrease = () => setNumber({ number: state.number + 1 });
  const handleDecrease = () => setNumber({ number: state.number - 1 });
  const handleC = (data) => {
    const { x, fixity, extf } = coords;

    fixity.push(0);
    extf.push({ x: 0, y: 0 });
    x.push([parseInt(data.x), parseInt(data.y)]);

    setCoords({ ...coords, x, fixity, extf });
  };

  const handleRemoveNode = (id) => {
    const { x, fixity, extf } = coords;
    let { nodes, secprops } = connectivity;

    x.splice(id, 1);
    fixity.splice(id, 1);
    extf.splice(id, 1);

    secprops = secprops.filter((item, ind) => !nodes[ind].includes(id));
    nodes = nodes.filter((a) => !a.includes(id));

    for (let i of nodes) {
      for (let j of i) {
        if (j > id) {
          const index1 = nodes.indexOf(i);
          const index2 = i.indexOf(j);
          nodes[index1][index2] -= 1;
        }
      }
    }

    setConnectivity({
      ...connectivity,
      nodes,
      secprops,
    });
    setCoords({ ...coords, x, fixity, extf });
    setRenders([]);
  };

  const handleRemoveElement = (id) => {
    let { nodes, secprops } = connectivity;

    nodes.splice(id, 1);
    secprops.splice(id, 1);

    setConnectivity({
      ...connectivity,
      nodes,
      secprops,
    });
    setRenders([]);
  };

  const handleChangeConnectivity = (activeNode, id) => {
    const newnodes = [...connectivity.nodes];
    const newsecprops = [...connectivity.secprops];

    newnodes.push([activeNode, id]);
    newsecprops.push({ A: 1, E: 1 });
    setConnectivity({
      ...connectivity,
      nodes: newnodes,
      secprops: newsecprops,
    });
    setRenders([]);
  };

  const handleChangeFixity = (id, newRestrain) => {
    const newfixity = [...coords.fixity];
    newfixity[id] = parseInt(newRestrain);
    setCoords({ ...coords, fixity: newfixity });
    setRenders([]);
  };

  const handleAssignForce = (e, id) => {
    const name = e.target.name;
    const value = parseInt(e.target.value);
    const copyforces = [...coords.extf];
    copyforces[id][name] = value;
    setCoords({ ...coords, extf: copyforces });
    setRenders([]);
  };

  const handleAssignSecProps = (e, id) => {
    const name = e.target.name;
    const value = parseInt(e.target.value);
    const copysecprops = [...connectivity.secprops];
    copysecprops[id][name] = value;
    setConnectivity({ ...connectivity, secprops: copysecprops });
    setRenders([]);
  };

  const liftSolution = (Du, Qu) => {
    setSolution({ ...solution, Du, Qu });
    toggleSolved(1);
    let node = 0;
    let flag = 0;

    let renderers = [];
    for (let i of coords.fixity) {
      if (i === 1) {
        const x1 = coords.x[node][0];
        const y1 = coords.x[node][1];
        const Fx = Qu[2 * flag];
        const Fy = Qu[2 * flag + 1];
        renderers.push(
          <g>
            <defs>
              <marker
                id="arrow"
                viewBox="0 -5 10 10"
                refX="5"
                refY="0"
                markerWidth="4"
                markerHeight="4"
                orient="auto"
                fill="teal"
              >
                <path d="M0,-5L10,0L0,5"></path>
              </marker>
            </defs>
            <line
              x1={x1}
              y1={y1}
              x2={x1 + (Fx / Math.abs(Fx)) * 30}
              y2={y1}
              stroke="teal"
              strokeWidth="4"
              markerEnd="url(#arrow)"
            ></line>
            <text
              x={x1 + (Fx / Math.abs(Fx)) * 30}
              y={y1 - (Fy / Math.abs(Fy)) * 25}
              fill="red"
            >
              Rx: {Fx.toFixed(2)}
            </text>
          </g>
        );

        renderers.push(
          <g>
            <defs>
              <marker
                id="arrow"
                viewBox="0 -5 10 10"
                refX="5"
                refY="0"
                markerWidth="4"
                markerHeight="4"
                orient="auto"
              >
                <path d="M0,-5L10,0L0,5"></path>
              </marker>
            </defs>
            <line
              x1={x1}
              y1={y1}
              x2={x1}
              y2={y1 + (Fy / Math.abs(Fy)) * 30}
              stroke="teal"
              strokeWidth="4"
              markerEnd="url(#arrow)"
            ></line>

            <text x={x1 + 10} y={y1 + (Fy / Math.abs(Fy)) * 30} fill="red">
              Ry: {Fy.toFixed(2)}
            </text>
          </g>
        );
        flag += 1;
      }
      node += 1;
    }
    const texts = document.getElementsByClassName("nodeTag");
    for (let i of texts) {
      i.style.visibility = "hidden";
    }
    setRenders(renderers);
  };

  const zoomIn = () => {
    const svg = document.getElementById("svg");

    let svgWidth = parseFloat(
      window
        .getComputedStyle(document.getElementById("svg"))
        .width.split("px")[0]
    );

    let svgHeight = parseFloat(
      window
        .getComputedStyle(document.getElementById("svg"))
        .height.split("px")[0]
    );
    svgWidth *= 1.25;
    svgHeight *= 1.25;
    svg.style.width = svgWidth;
    svg.style.height = svgHeight;
  };

  const zoomOut = () => {
    const svg = document.getElementById("svg");

    let svgWidth = parseFloat(
      window
        .getComputedStyle(document.getElementById("svg"))
        .width.split("px")[0]
    );

    let svgHeight = parseFloat(
      window
        .getComputedStyle(document.getElementById("svg"))
        .height.split("px")[0]
    );
    svgWidth *= 0.8;
    svgHeight *= 0.8;
    svg.style.width = svgWidth;
    svg.style.height = svgHeight;
  };

  return (
    <React.Fragment>
      <div className="flexbox-container">
        <div className="sidebar-flexbox-container">
          <InputNodes className="sidebar-flexbox-item" addCoord={handleC} />
          {/*<Compo
            no={state.number}
            onIncrease={handleIncrease}
            onDecrease={handleDecrease}
  />*/}

          <CoordsList
            className="sidebar-flexbox-item"
            cort={coords}
            removeNode={handleRemoveNode}
            changeFixity={handleChangeFixity}
            assignForce={handleAssignForce}
          />
          <ElementsList
            className="sidebar-flexbox-item"
            connectivity={connectivity}
            handleAssignSecProps={handleAssignSecProps}
            removeElement={handleRemoveElement}
          />
          <Solve
            className="sidebar-flexbox-item"
            solved={solved}
            coords={coords}
            connectivity={connectivity}
            liftSolution={liftSolution}
          />
        </div>
        <div className="content">
          <Circle
            solved={solved}
            cort={coords}
            test={state.number}
            changeConnectivity={handleChangeConnectivity}
            connect={connectivity}
            renders={renders}
            zoomIn={zoomIn}
            zoomOut={zoomOut}
          />
        </div>
      </div>
    </React.Fragment>
  );
}
