import React from "react";

const CoordsList = (props) => {
  return (
    <ul className="coords-list">
      {props.cort.x.map((x, id) => (
        <li key={id}>
          <span className="coords-text">
            Node {id + 1}: ({x[0]}, {x[1]})
          </span>
          <br />
          Fx:&nbsp;
          <input
            type="number"
            name="x"
            value={props.cort.extf[id].x}
            onChange={(e) => props.assignForce(e, id)}
          ></input>
          Fy:&nbsp;
          <input
            type="number"
            name="y"
            value={props.cort.extf[id].y}
            onChange={(e) => props.assignForce(e, id)}
          ></input>
          &nbsp;
          <select
            name="support"
            id="support"
            onChange={(e) => props.changeFixity(id, e.target.value)}
          >
            <option selected={props.cort.fixity[id]} value="0">
              Free
            </option>
            <option selected={props.cort.fixity[id]} value="1">
              Pin
            </option>
          </select>
          <button
            className="btn btn-danger"
            onClick={() => props.removeNode(id)}
          >
            X
          </button>
        </li>
      ))}
    </ul>
  );
};

export default CoordsList;
