import React from "react";
import * as math from "mathjs";

const Solve = (props) => {
  const { solved, coords, connectivity, liftSolution } = props;

  const handleClick = () => {
    let K = Array(coords.x.length * 2)
      .fill()
      .map(() => Array(coords.x.length * 2).fill(0));

    for (let i of connectivity.nodes) {
      const xn = coords.x[i[0]][0];
      const yn = coords.x[i[0]][1];
      const xf = coords.x[i[1]][0];
      const yf = coords.x[i[1]][1];
      let L = Math.sqrt((xf - xn) ** 2 + (yf - yn) ** 2); // sqrt((xfar-xnear)^2+(yfar-ynear)^2)
      let Lx = (xf - xn) / L;
      let Ly = (yf - yn) / L;

      const index = connectivity.nodes.indexOf(i);
      const E = connectivity.secprops[index].E;
      const A = connectivity.secprops[index].A;
      K[i[0] * 2][i[0] * 2] += ((A * E) / L) * Lx ** 2;
      K[i[1] * 2][i[1] * 2] += ((A * E) / L) * Lx ** 2;

      K[i[0] * 2][i[0] * 2 + 1] += ((A * E) / L) * Lx * Ly;
      K[i[1] * 2][i[1] * 2 + 1] += ((A * E) / L) * Lx * Ly;
      K[i[0] * 2 + 1][i[0] * 2] += ((A * E) / L) * Lx * Ly;
      K[i[1] * 2 + 1][i[1] * 2] += ((A * E) / L) * Lx * Ly;

      K[i[0] * 2][i[1] * 2] += ((A * E) / L) * -1 * Lx ** 2;
      K[i[1] * 2][i[0] * 2] += ((A * E) / L) * -1 * Lx ** 2;

      K[i[0] * 2 + 1][i[1] * 2 + 1] += ((A * E) / L) * -1 * Ly ** 2;
      K[i[1] * 2 + 1][i[0] * 2 + 1] += ((A * E) / L) * -1 * Ly ** 2;

      K[i[0] * 2 + 1][i[0] * 2 + 1] += ((A * E) / L) * Ly ** 2;
      K[i[1] * 2 + 1][i[1] * 2 + 1] += ((A * E) / L) * Ly ** 2;

      K[i[0] * 2][i[1] * 2 + 1] += ((A * E) / L) * -1 * Lx * Ly;
      K[i[1] * 2 + 1][i[0] * 2] += ((A * E) / L) * -1 * Lx * Ly;
      K[i[0] * 2 + 1][i[1] * 2] += ((A * E) / L) * -1 * Lx * Ly;
      K[i[1] * 2][i[0] * 2 + 1] += ((A * E) / L) * -1 * Lx * Ly;
    }
    let K11_flags = [];
    let Qk = [];
    for (let [v, fix] of coords.fixity.entries()) {
      if (!fix) {
        K11_flags.push(1);
        K11_flags.push(1);
        Qk.push(coords.extf[v].x);
        Qk.push(coords.extf[v].y);
      } else {
        K11_flags.push(0);
        K11_flags.push(0);
      }
    }
    //console.log("Qk", Qk, "K11_flags", K11_flags);
    let K11 = K.filter((item, id) => K11_flags[id] === 1).map((item) =>
      item.filter((item, id) => K11_flags[id] === 1)
    );

    let K21 = K.filter((item, id) => K11_flags[id] === 0).map((item) =>
      item.filter((item, id) => K11_flags[id] === 1)
    );

    //console.table(K21);
    const Du = math.multiply(math.inv(K11), Qk);
    const Qu = math.multiply(K21, Du);

    liftSolution(Du, Qu);
    //console.table(K);
    //console.table(Qk);
  };
  return (
    <button onClick={handleClick} className="btn btn-lg btn-success">
      Solve
    </button>
  );
};

export default Solve;
