import React from "react";

const ElementsList = (props) => {
  const { connectivity, handleAssignSecProps } = props;
  //console.table(K);
  //console.table(Qk);

  return (
    <ul className="elements-list">
      {connectivity.nodes.map((x, id) => (
        <li className="elements-item" key={id}>
          Element {id + 1} <br />
          {/*From Node: {x[0]}, to Node: {x[1]} --><br />*/}
          E-Modulus:&nbsp;
          <input
            type="number"
            name="E"
            value={connectivity.secprops[id].E}
            onChange={(e) => handleAssignSecProps(e, id)}
          ></input>{" "}
          Area:&nbsp;
          <input
            type="number"
            name="A"
            value={connectivity.secprops[id].A}
            onChange={(e) => handleAssignSecProps(e, id)}
          ></input>
          <button
            className="btn btn-danger"
            onClick={() => props.removeElement(id)}
          >
            X
          </button>
        </li>
      ))}
    </ul>
  );
};

export default ElementsList;
