import React, { useEffect, useState } from "react";

const Circle = (props) => {
  var [anyNodeSelected, setAnyNodeSelected] = useState(false);
  var [activeNode, setNewActiveNode] = useState();
  var [line, setLine] = useState();
  var [mouseCoords, setMouseCoords] = useState({ x: 0, y: 0 });
  var [supportForces, setSupportForces] = useState();
  const viewBoxX = 0;
  const viewBoxY = 0;
  let viewBoxWidth = 800;
  let viewBoxHeight = 500;

  /*document
    .getElementById("svg")
    .setAttribute(
      "viewBox",
      `${viewBoxX},${viewBoxY},${viewBoxWidth},${viewBoxHeight}`
    );*/

  for (let i of props.cort.x) {
    /*if (i[0] > xMax) {
      xMax = i[0];
    }
    if (i[1] > yMax) {
      yMax = i[1];
    }*/
    var stayl = {
      height: 500,
      width: 800,
      backgroundColor: "#ccfcff",
    };
  }

  const handleMouseClick = (id) => {
    if (!anyNodeSelected) {
      setNewActiveNode(id);
      setAnyNodeSelected(true);
    } else {
      props.changeConnectivity(activeNode, id);
      setNewActiveNode(id);
      /*const newnodes = [...connectivity];
      newnodes.push([activeNode, id]);
      setConnectivity(newnodes);*/
    }
  };

  const handleMouseMove = (e) => {
    let svgWidth = parseFloat(
      window
        .getComputedStyle(document.getElementById("svg"))
        .width.split("px")[0]
    );

    let svgHeight = parseFloat(
      window
        .getComputedStyle(document.getElementById("svg"))
        .height.split("px")[0]
    );
    const x = e.nativeEvent.offsetX;
    const y = e.nativeEvent.offsetY;
    const svg = document.getElementById("svg");
    var viewbox = svg.getAttributeNS(null, "viewBox").split(",");
    const viewBoxX = parseInt(viewbox[0]);
    const viewBoxY = parseInt(viewbox[1]);
    let viewBoxWidth = viewbox[2];
    let viewBoxHeight = viewbox[3];
    const globX = (x / svgWidth) * viewBoxWidth + viewBoxX;
    const globY = (y / svgHeight) * viewBoxHeight + viewBoxY;
    setMouseCoords({
      x: globX,
      y: globY,
    });

    /*setMouseCoords({
      x: e.nativeEvent.offsetX,
      y: e.nativeEvent.offsetY,
    });*/
    if (anyNodeSelected) {
      setLine(
        <line
          x1={props.cort.x[activeNode][0]}
          y1={props.cort.x[activeNode][1]}
          x2={mouseCoords.x}
          y2={mouseCoords.y}
          stroke="black"
          stroke-width="3"
        />
      );
    }
  };

  useEffect(() => {
    function handleKeyDown(e) {
      if (e.keyCode === 27) {
        setAnyNodeSelected(false);
        setNewActiveNode();
        setLine();
      }
    }
    document.addEventListener("keydown", handleKeyDown);

    // Don't forget to clean up
    return function cleanup() {
      document.removeEventListener("keydown", handleKeyDown);
    };
  }, []);

  const drawSupport = (id) => {
    const x1 = parseFloat(props.cort.x[id][0]);
    const y1 = parseFloat(props.cort.x[id][1]);
    const x2 = parseFloat(x1 + 10);
    const y2 = parseFloat(y1 + 20);
    const x3 = parseFloat(x1 - 10);
    const y3 = parseFloat(y1 + 20);

    return (
      //<line x1={x1} y1={y1} x2={x2} y2={y2} strokeWidth="3" stroke="black" />
      <polygon
        points={`${x1},${y1} ${x2},${y2} ${x3},${y3}`}
        fill="#424963"
      ></polygon>
    );
  };

  const drawForce = (id) => {
    const x1 = parseInt(props.cort.x[id][0]);
    const y1 = parseInt(props.cort.x[id][1]);
    const Fx = parseInt(props.cort.extf[id]["x"]);
    const Fy = parseInt(props.cort.extf[id]["y"]);
    const xdraw = [];
    if (Fx) {
      xdraw.push(
        <g>
          <defs>
            <marker
              id="arrow"
              viewBox="0 -5 10 10"
              refX="5"
              refY="0"
              markerWidth="4"
              markerHeight="4"
              orient="auto"
              fill="teal"
            >
              <path d="M0,-5L10,0L0,5"></path>
            </marker>
          </defs>
          <line
            x1={x1}
            y1={y1}
            x2={x1 + (Fx / Math.abs(Fx)) * 30}
            y2={y1}
            stroke="teal"
            strokeWidth="4"
            markerEnd="url(#arrow)"
          ></line>
          <text x={x1 + 10} y={y1 - 10} fill="red">
            Fx: {Fx}
          </text>
        </g>
      );
    }
    if (Fy) {
      xdraw.push(
        <g>
          <defs>
            <marker
              id="arrow"
              viewBox="0 -5 10 10"
              refX="5"
              refY="0"
              markerWidth="4"
              markerHeight="4"
              orient="auto"
            >
              <path d="M0,-5L10,0L0,5"></path>
            </marker>
          </defs>
          <line
            x1={x1}
            y1={y1}
            x2={x1}
            y2={y1 + (Fy / Math.abs(Fy)) * 30}
            stroke="teal"
            strokeWidth="4"
            markerEnd="url(#arrow)"
          ></line>

          <text x={x1 - 60} y={y1 + 30} fill="red">
            Fy: {Fy}
          </text>
        </g>
      );
    }
    return xdraw;
  };

  return (
    <div>
      <div className="container" id="container">
        <svg
          id="svg"
          viewBox="0,0,800,500"
          //preserveAspectRatio="xMaxYMax meet"
          //style={stayl}
          onMouseMove={handleMouseMove}
        >
          <g>
            {props.cort.fixity.map((x, id) => (x ? drawSupport(id) : ""))}
            {props.connect.nodes.map((element, id) => (
              <g>
                <line
                  x1={props.cort.x[element[0]][0]}
                  y1={props.cort.x[element[0]][1]}
                  x2={props.cort.x[element[1]][0]}
                  y2={props.cort.x[element[1]][1]}
                  stroke="black"
                  stroke-width="3"
                />
                <text
                  x={
                    (props.cort.x[element[0]][0] +
                      props.cort.x[element[1]][0]) /
                    2
                  }
                  y={
                    (props.cort.x[element[0]][1] +
                      props.cort.x[element[1]][1]) /
                    2
                  }
                  fill="red"
                >
                  el: {id + 1}
                </text>
              </g>
            ))}
            {line}
            {props.cort.x.map((x, id) => (
              <g>
                <circle
                  onClick={() => handleMouseClick(id)}
                  cx={x[0]}
                  cy={x[1]}
                  r={props.test}
                  fill="blue"
                  className="circle"
                />
                <text className="nodeTag" x={x[0]} y={x[1] + 20} fill="red">
                  Node {id + 1}
                </text>
                {props.cort.extf.map((x, id) =>
                  x["x"] || x["y"] ? drawForce(id) : ""
                )}
              </g>
            ))}
            {props.renders}
          </g>
        </svg>
      </div>
      <br />
      <button className="btn btn-primary" onClick={props.zoomIn}>
        +
      </button>

      <button
        style={{ padding: "6px 15px", margin: "3px" }}
        className="btn btn-primary"
        onClick={props.zoomOut}
      >
        -
      </button>
      <h1>
        X: {parseInt(mouseCoords.x)} Y: {parseInt(mouseCoords.y)}
      </h1>
    </div>
  );
};

export default Circle;
